<template>
  <Loading :active="isLoading" :z-index="1060" />
  <h1>方案管理</h1>
  <div class="text-end">
    <button type="button" class="btn btn-primary text-white" @click="openPaintingModal(true)">
      建立方案
    </button>
  </div>
  <table class="table">
    <thead>
      <tr>
        <th width="120">序號</th>
        <th width="120">分類</th>
        <th>畫作名稱</th>
        <th>畫作</th>
        <th width="120">年份</th>
        <th width="120">單位</th>
        <th width="120">尺寸</th>
        <th width="120">售價</th>
        <th width="100">是否啟用</th>
        <th width="200">編輯</th>
      </tr>
    </thead>
    <tbody>
      <tr v-for="(project, index) in projects" :key="project.id">
        <td>{{ index + 1 }}</td>
        <td>{{ project.category }}</td>
        <td>{{ project.title }}</td>
        <td>
          <img :src="project.imageUrl" style="height: 100px" :alt="project.title" />
        </td>
        <td>{{ project.year }}</td>
        <td>{{ project.unit }}</td>
        <td class="text-right">{{ project.size }}</td>
        <td class="text-right">{{ project.price }}</td>
        <td>
          <span v-if="project.is_enabled" class="text-success">啟用</span>
          <span v-else>未啟用</span>
        </td>
        <td>
          <div class="btn-group">
            <button
              type="button"
              class="btn btn-outline-primary btn-sm"
              @click="openPaintingModal(false, project)"
            >
              編輯
            </button>
            <button
              type="button"
              class="btn btn-outline-danger btn-sm"
              @click="openDeleteModal(project)"
            >
              刪除
            </button>
          </div>
        </td>
      </tr>
    </tbody>
  </table>
  <div class="d-flex justify-content-center">
    <Pagination :pages="pagination" @emit-page="getProjects" />
  </div>
  <ProjectModal
    ref="projectModal"
    :project="tempPainting"
    :isNew="isNew"
    @update-project="updatePainting"
  />
  <DeleteModal ref="deleteModal" :item="tempPainting" @delete-item="deletePainting" />
</template>

<script>
import ProjectModal from '@/components/modals/ProjectModal.vue';
import DeleteModal from '@/components/modals/DeleteModal.vue';
import Pagination from '@/components/Pagination.vue';

export default {
  data() {
    return {
      isLoading: false,
      projects: [],
      pagination: {},
      tempPainting: {},
      isNew: false,
    };
  },
  components: {
    ProjectModal,
    DeleteModal,
    Pagination,
  },
  methods: {
    getProjects(page = 1) {
      this.isLoading = true;
      this.$http
        .get(
          `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/admin/products?page=${page}`,
        )
        .then((res) => {
          this.projects = res.data.products;
          this.pagination = res.data.pagination;
          this.isLoading = false;
        })
        .catch((err) => {
          this.isLoading = false;
          this.$httpMessageState(err.response, '錯誤訊息');
        });
    },
    openPaintingModal(isNew, project) {
      this.isNew = isNew;

      if (isNew) {
        this.tempPainting = {};
      } else {
        this.tempPainting = { ...project };
      }

      this.$refs.projectModal.openModal();
    },
    updatePainting(project) {
      this.isLoading = true;
      this.tempPainting = project;

      let api = `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/admin/product/`;
      let httpMethod = 'post';
      let state = '新增畫作';

      if (!this.isNew) {
        api = `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/admin/product/${project.id}`;
        httpMethod = 'put';
        state = '更新畫作';
      }

      this.$http[httpMethod](api, { data: this.tempPainting })
        .then((res) => {
          this.getProjects(this.pagination.current_page);
          this.$refs.projectModal.closeModal();
          this.isLoading = false;
          this.$httpMessageState(res, state);
        })
        .catch((err) => {
          this.isLoading = false;
          this.$httpMessageState(err.response);
        });
    },
    openDeleteModal(project) {
      this.tempPainting = { ...project };
      this.$refs.deleteModal.openModal();
    },
    deletePainting(paintingId) {
      this.$http
        .delete(
          `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/admin/product/${paintingId}`,
        )
        .then((res) => {
          this.getProjects(this.pagination.current_page);
          this.isLoading = false;
          this.$httpMessageState(res, '刪除畫作');
          this.$refs.deleteModal.closeModal();
        })
        .catch((res) => {
          this.isLoading = false;
          this.$httpMessageState(res.response, '刪除畫作');
        });
    },
  },
  mounted() {
    this.getProjects();
  },
};
</script>
